import React, { useState, useEffect, useCallback } from "react";
import "./walletModal.less";
import Icon from "../Icon/Icon";
import { hide } from "react-functional-modal";
import Dialog from "../Dialog/Dialog";
import { SUPPORTED_WALLETS } from "../../utils/Constants";
import Spinner from "../Spinner/Spinner";
import { useWallet } from "use-wallet";
import config from "../../page-network-config.json";
import { useLocation } from "react-router-dom";
import Wallet from "../../model/Wallet";
import { buried, getDefaultNw } from "../../utils/utils";
import { DeriEnv } from "../../lib/web3js";
import { useMemo } from "react";

export default function WalletModal({lang, closeModal, isWrongNetwork, wallet }) {
  const walletContext = useWallet();
  const footer = (
    <div className="wallet-modal-footer">
      <a href="https://metamask.io/faqs" rel="noreferrer" target="_blank">
        {lang["learn-more-about-wallet"]}
      </a>
    </div>
  );
  const curConnector = useMemo(() => {
    const connector = localStorage.getItem("connector")
    if(connector){
      return JSON.parse(connector)
    }
    return Object.values(SUPPORTED_WALLETS).find(obj => obj.name === 'MetaMask')
  }
  , [])
  
  return (
    <Dialog
      title={lang["connect-wallet"]}
      footer={footer}
      className="wallet-modal"
      onClose={closeModal}
    >
      <div className="wallet-modal-body">
        {Object.keys(SUPPORTED_WALLETS).map((key, index) => (
          <Option
            walletContext={walletContext}
            isWrongNetwork={isWrongNetwork}
            wallet={wallet}
            connected={
              walletContext.status === "connected" &&
              SUPPORTED_WALLETS[key].name === curConnector.name
            }
            closeModal={closeModal}
            name={SUPPORTED_WALLETS[key].name}
            connector={SUPPORTED_WALLETS[key].connector}
            key={index}
            icon={SUPPORTED_WALLETS[key].icon}

          />
        ))}
      </div>
    </Dialog>
  );
}

const Option = ({
  walletContext,
  connector,
  name,
  icon,
  index,
  connected,
  closeModal,
  isWrongNetwork,
  wallet,
}) => {
  const [connecting, setConnecting] = useState(false);
  const chainList = config[DeriEnv.get()];

  const connect = useCallback(
    async (key) => {
      setConnecting(true);
      let wallet_name = "metamask";
      if (key === "injected") {
        wallet_name = "metamask";
      } else if (key === "walletconnect") {
        wallet_name = "walletconnect";
      } else if (key === "walletlink") {
        wallet_name = "coinbase";
      }
      buried("wallet_selection", "wallet", wallet_name);   
      
      if(walletContext.isConnected() && walletContext.connector === 'walletconnect') {
        await walletContext._web3ReactContext.connector.close()
      }   
      await walletContext.connect(key);
      // if(walletContext.isConnected()) {
        const connector = Object.values(SUPPORTED_WALLETS).find(obj => obj.name === name)
        localStorage.setItem('connector',JSON.stringify(connector))
        closeModal();
      // }
      setConnecting(false);
    },
    [walletContext]
  );

  useEffect(async () => {
    // if (walletContext.providerInfo && walletContext.status === "connected") {
    //   if (isWrongNetwork()) {
    //     const defaultNetwork = getDefaultNw(DeriEnv.get());
    //     await wallet.switchNetwork(
    //       defaultNetwork,
    //       () => {
    //         closeModal();
    //       },
    //       async () => {
    //         const ethereum = window.ethereum;
    //         await walletContext.reset();
    //         if (ethereum) {
    //           window.ethereum = ethereum;
    //         }
    //       }
    //     );
    //   }
    //   setConnecting(false);
    // }
    if (walletContext.status === "error") {
      setConnecting(false);
    }
  }, [walletContext.status]);
  return (
    <div className="wallet-modal-body-item" key={index} onClick={() => connect(connector)}>
      <div className="name">
        <div className="status">{connected && <div className="connected"></div>}</div>
        {name}
        {connecting && <Spinner width="14" height="14" />}
      </div>
      <Icon token={icon} width="38" height="38" />
    </div>
  );
};
